import React from 'react'
import PropTypes from 'prop-types'
import logoImage1 from '../images/darkmode.png'
import logoImage2 from '../images/lightmode.png'
import { Link } from "react-router-dom";


export default function Navbar(props) {
  const imgStyle = {
    width: "100px",
    objectFit: "contain"
  }
  

  return (
    <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  <div className="container">
    {/* <a className="navbar-brand " href="/">{props.title}</a> */}
    <Link className="navbar-brand " to="/">{props.mode === "dark"? (<img src={logoImage1} alt=""  style={imgStyle}/>): (<img src={logoImage2} alt=""  style={imgStyle}/>)}</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="true" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/about">{props.aboutText}</Link>
        </li>

      </ul>

        <div className={`form-check form-switch text-${props.mode === "light"? "dark" :"light"}`}>
          <input className="form-check-input" onClick={props.toggleMode} type="checkbox" id="flexSwitchCheckDefault"/>
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault" >Enable Dark Mode</label>
        </div>
    </div>
  </div>
</nav>
  )
}

Navbar.prototypes = {
    title: PropTypes.string.isRequired,
    aboutText: PropTypes.string
}

Navbar.defaultProps = {
    title: 'Yahya',
    aboutText: 'About',
    darkSrc: './images/Dark Mode.png',
    lightSrc: ''
}
